@import url(https://fonts.googleapis.com/css2?family=Chango&family=Londrina+Shadow&family=Rubik:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
  background: #262629;

}

.contenedor{
  /* position: fixed; */
    margin: 0 auto;
    width: 100%;
}

.promo{
  width: 100%;
  padding-top: 30px;
}


@media (max-width: 600px) {
  .promo{
      height: 130px;
    } 
}
/* Style the header */
header {
    /* background-color: #6d1201; */
    /* padding: 30px; */
    /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#020925+0,6d1606+100 */
  background: #010721; /* Old browsers */ 
  background: linear-gradient(to right,  #010721 0%,#6d1606 90%); 
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#010721', endColorstr='#6d1606',GradientType=1 ); 
  
    text-align: center;
    font-size: 15px;
    color: white;
  }
  header img{
    height: 25vh;
    width: 60vw;
  }
  
@media (max-width: 600px) {
    header img{
      height: 20vh;
    }
  }

.footer-social-media{
    display: -webkit-inline-flex;
    display: inline-flex;
    margin: 0 auto;
  }
  
.App-link-social-media{
    color: rgb(107, 214, 7);
    font-family: 'Rubik', sans-serif;
  }
  
  a:link,
  a:hover,
  a:active,
  a:visited {
    text-decoration: none;
    color: green;
  }
  
  
  .link-social-media{
    display: grid;
    padding: 0px 5px;
    font-size: 15px;
    text-align: left;
    font-family: 'Londrina Shadow', cursive;
  }
  .icon-social-media{
    font-size: 50px;
    color: #b1a8a8;
  }

footer{
    background: #181819;
    /* position: fixed; */
      bottom: 0px;
      text-align: center;
      width: 100%;
  }
  
  footer h2{
    color: rgb(107, 214, 7);
    font-family: 'Londrina Shadow', cursive;
    text-shadow: 0px 1px 10px rgba(255, 151, 31, 0.69)
  }

  .btn-candela{
    width: 20%; 
    min-height: 20%; 
    background: #000000
  }
  
@media (max-width: 600px) {
    header img{
      height: 20vh;
    }
    .btn-candela{
        width: 25%; 
    }
  }

.container-publicidad{
  padding-top: 15px;
  text-align: center;

}

.posts-images{
    width: 70vw;
    height: 20vh;
    margin: 0 auto;
  } 

  @media (max-width: 600px) {
    .posts-images{
        width: 100vw;
        height: 12vh;
        margin: 0 auto;
      } 
  }

  .hidden{
      display: none;
  }
